.content {
	figure {
		width: 100%;
		height: 300px;
		background: #bbb;
	}

	.inner {
		width: 1180px;
		min-height: 400px;
		margin: 100px auto 150px;
		position: relative;

		h1 {
			font: normal 30px/1 'arial';
			color: #777;
			margin-bottom: 50px;
		}
	}
}

@media screen and (max-width: $tablet) {
	.content {
		.inner {
			width: 100%;
			padding: 0px 40px;
		}
	}
}
